import {lazy, Suspense} from "react";
import Fallback, {type PageProps} from "keycloakify/login";
import type {KcContext} from "./kcContext";
import {useI18n} from "./i18n";
import Template from "./Template";
import LoginResetPassword from "./pages/LoginResetPassword";
import LoginUsername from "./pages/LoginUsername";
import LoginPassword from "./pages/LoginPassword";
import WebauthnRegister from "./pages/WebauthnRegister";
import WebauthnAuthenticate from "./pages/WebauthnAuthenticate";
import SelectAuthenticator from "./pages/SelectAuthenticator";
import Error from "./pages/Error";
import WebauthnError from "./pages/WebauthnError";
import LoginConfigTotp from "./pages/LoginConfigTotp";
import LoginUpdatePassword from "./pages/LoginUpdatePassword";
import LoginOtp from "./pages/LoginOtp";
import SamlPostForm from "./pages/SamlPostForm";
import LoginDeviceVerifyUserCode from "./pages/LoginDeviceVerifyUserCode";
import LoginOauthGrant from "./pages/LoginOauthGrant";
import LoginPageExpired from "./pages/LoginPageExpired";
import LoginVerifyEmail from "./pages/LoginVerifyEmail";
import LoginIdpLinkConfirm from "./pages/LoginIdpLinkConfirm";
import IdpReviewUserProfile from "./pages/IdpReviewUserProfile";
import LoginIdpLinkEmail from "./pages/LoginIdpLinkEmail";
import LogoutConfirm from "./pages/LogoutConfirm";
import LoginPasskeyOrPasswordStories from "./pages/LoginPasskeyOrPassword.stories";
import LoginPasskeyOrPassword from "./pages/LoginPasskeyOrPassword";

const Login = lazy(() => import("./pages/Login"));
// If you can, favor register-user-profile.ftl over register.ftl, see: https://docs.keycloakify.dev/realtime-input-validation
const Register = lazy(() => import("./pages/Register"));
const RegisterUserProfile = lazy(() => import("./pages/RegisterUserProfile"));
const Terms = lazy(() => import("./pages/Terms"));
const MyExtraPage1 = lazy(() => import("./pages/MyExtraPage1"));
const MyExtraPage2 = lazy(() => import("./pages/MyExtraPage2"));
const Info = lazy(() => import("./pages/Info"));

// This is like adding classes to theme.properties 
// https://github.com/keycloak/keycloak/blob/11.0.3/themes/src/main/resources/theme/keycloak/login/theme.properties
const classes: PageProps<any, any>["classes"] = {
    // NOTE: The classes are defined in ./KcApp.css
    "kcHtmlClass": "my-root-class",
    "kcHeaderWrapperClass": "my-color my-font"
};

export default function KcApp(props: { kcContext: KcContext; }) {

    const {kcContext} = props;

    const i18n = useI18n({kcContext});

    if (i18n === null) {
        //NOTE: Text resources for the current language are still being downloaded, we can't display anything yet.
        //We could display a loading progress but it's usually a matter of milliseconds.
        return null;
    }

    /* 
    * Examples assuming i18n.currentLanguageTag === "en":
    * i18n.msg("access-denied") === <span>Access denied</span>
    * i18n.msg("foo") === <span>foo in English</span>
    */
    return (
        <Suspense>
            {(() => {
                switch (kcContext.pageId) {
                    case "login.ftl":
                        return <Login {...{kcContext, i18n, Template, classes}} doUseDefaultCss={false}/>;
                    case "login-username.ftl":
                        return <LoginUsername {...{kcContext, i18n, Template, classes}} doUseDefaultCss={false}/>;
                    case "login-password.ftl":
                        return <LoginPassword {...{kcContext, i18n, Template, classes}} doUseDefaultCss={false}/>;
                    case "login-reset-password.ftl":
                        return <LoginResetPassword {...{kcContext, i18n, Template, classes}} doUseDefaultCss={false}/>;
                    case "register.ftl":
                        return <Register {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case "register-user-profile.ftl":
                        return <RegisterUserProfile {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>
                    case "terms.ftl":
                        return <Terms {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    // Removes those pages in you project. They are included to show you how to implement keycloak pages
                    // that are not yes implemented by Keycloakify. 
                    // See: https://docs.keycloakify.dev/limitations#some-pages-still-have-the-default-theme.-why
                    case "my-extra-page-1.ftl":
                        return <MyExtraPage1 {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case "my-extra-page-2.ftl":
                        return <MyExtraPage2 {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    // We choose to use the default Template for the Info page and to download the theme resources.
                    // This is just an example to show you what is possible. You likely don't want to keep this as is. 
                    case 'webauthn-authenticate.ftl':
                        return <WebauthnAuthenticate {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'select-authenticator.ftl':
                        return <SelectAuthenticator {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'webauthn-register.ftl':
                        return <WebauthnRegister {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'webauthn-error.ftl':
                        return <WebauthnError {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'login-config-totp.ftl':
                        return <LoginConfigTotp {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'login-update-password.ftl':
                        return <LoginUpdatePassword {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'login-otp.ftl':
                        return <LoginOtp {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case 'error.ftl':
                        return <Error {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case "info.ftl":
                        return (
                            <Info {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>);
                    case "saml-post-form.ftl":
                        return <SamlPostForm {...{kcContext, i18n, Template, classes}} doUseDefaultCss={true}/>;
                    case "login-oauth2-device-verify-user-code.ftl":
                        return <LoginDeviceVerifyUserCode {...{kcContext, i18n, Template, classes}}
                                                          doUseDefaultCss={true}/>;
                    case "login-oauth-grant.ftl":
                        return <LoginOauthGrant {...{kcContext, i18n, Template, classes}}
                                                doUseDefaultCss={true}/>;
                    case "login-page-expired.ftl":
                        return <LoginPageExpired {...{kcContext, i18n, Template, classes}}
                                                 doUseDefaultCss={true}/>;
                    case "login-verify-email.ftl":
                        return <LoginVerifyEmail {...{kcContext, i18n, Template, classes}}
                                                 doUseDefaultCss={true}/>;
                    case "login-idp-link-confirm.ftl":
                        return <LoginIdpLinkConfirm {...{kcContext, i18n, Template, classes}}
                                                    doUseDefaultCss={true}/>;
                    case "idp-review-user-profile.ftl":
                        return <IdpReviewUserProfile {...{kcContext, i18n, Template, classes}}
                                                     doUseDefaultCss={true}/>;
                    case "login-idp-link-email.ftl":
                        return <LoginIdpLinkEmail {...{kcContext, i18n, Template, classes}}
                                                  doUseDefaultCss={true}/>;
                    case "logout-confirm.ftl":
                        return <LogoutConfirm {...{kcContext, i18n, Template, classes}}
                                                  doUseDefaultCss={true}/>;
                    case "passkey-login-form.ftl":
                        return <LoginPasskeyOrPassword {...{kcContext, i18n, Template, classes}}
                                                       doUseDefaultCss={true}/>;
                    default:
                        return <Fallback {...{kcContext, i18n, classes}} Template={Template} doUseDefaultCss={true}/>;
                }
            })()}
        </Suspense>
    );

}
