import {useState, type FormEventHandler, useEffect} from "react";
import {clsx} from "keycloakify/tools/clsx";
import {useConstCallback} from "keycloakify/tools/useConstCallback";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Input} from "../../components/ui/input";
import {Label} from "../../components/ui/label";
import {Button} from "../../components/ui/button";
import {useTitle} from "react-use";


export default function LoginResetPassword(props: PageProps<Extract<KcContext, {
    pageId: "login-reset-password.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {url, realm, auth,recaptchaRequired,recaptchaSiteKey} = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(`${msgStr("emailForgotTitle")} - ${kcContext.realm.displayName}`)
    useEffect(() => {
        if (recaptchaRequired && recaptchaSiteKey) {
            const recaptchaScriptUrl = kcContext.scripts?.[0] || "https://www.google.com/recaptcha/api.js"; // fallback for Storybook

            let script = document.querySelector(`script[src="${recaptchaScriptUrl}"]`) as HTMLScriptElement | null;

            const renderRecaptcha = () => {
                if (window.grecaptcha && document.getElementById("recaptcha-container")) {
                    try {
                        window.grecaptcha.render("#recaptcha-container", {
                            sitekey: recaptchaSiteKey
                            // other reCAPTCHA options as needed
                        });
                    } catch (e) {

                    }

                }

            };

            if (!script) {
                script = document.createElement("script");
                script.src = recaptchaScriptUrl;
                script.async = true;
                document.head.appendChild(script);

                script.onload = () => {
                    setTimeout(renderRecaptcha, 1000);
                };
            } else {
                setTimeout(renderRecaptcha, 1000);
            }

            return () => {
                if (document.body.contains(script)) {
                    document.body.removeChild(script!);
                }
            };
        }
    }, [recaptchaRequired, recaptchaSiteKey, kcContext.scripts]);
    return (
        <Template
            {...{kcContext, i18n, doUseDefaultCss, classes}}
            displayMessage={false}
            headerNode={msg("emailForgotTitle")}
            infoNode={msg("emailInstruction")}
        >
            <div className="lg:p-8 lg:pb-4 flex gap-4 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center md:w-[350px] `}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    {/*<div id="kc-form" className={clsx(realm.password && social.providers !== undefined && getClassName("kcContentWrapperClass"))}>*/}
                    {/*    <div*/}
                    <form id="kc-reset-password-form" className={"flex flex-col gap-3 items-center w-full"} action={url.loginAction}
                          method="post">
                        <div className="flex flex-col w-full max-w-sm  gap-1.5">
                            <Label htmlFor="autoCompleteHelper"> {!realm.loginWithEmailAllowed
                                ? msg("username")
                                : !realm.registrationEmailAsUsername
                                    ? msg("usernameOrEmail")
                                    : msg("email")}</Label>
                            <Input
                                type="text"
                                id="username"
                                name="username"
                                autoFocus
                                defaultValue={auth !== undefined && auth.showUsername ? auth.attemptedUsername : undefined}
                            />
                        </div>
                        {recaptchaRequired && (
                            <div className="form-group">
                                <div className={getClassName("kcInputWrapperClass")}>
                                    <div className="g-recaptcha w-full first:w-full" data-size="normal"
                                         id="recaptcha-container" data-sitekey={recaptchaSiteKey}/>
                                </div>
                            </div>
                        )}
        
                            <Button
                                className={"w-full"}
                                type="submit">
                                {msgStr("doSubmit")}
                            </Button>
                       

                    </form>


                    <div className={"text-sm mt-3 hover:underline text-slate-500"}>
                            <span>
                                <a href={url.loginUrl}>{msg("backToLogin")}</a>
                            </span>
                    </div>
                </div>
            </div>
        </Template>
    );
}
