import {useState} from "react";
import {clsx} from "keycloakify/tools/clsx";

import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Button} from "../../components/ui/button";
import {UserProfileFormFields} from "./shared/UserProfileFormFields";

export default function IdpReviewUserProfile(props: PageProps<Extract<KcContext, {
    pageId: "idp-review-user-profile.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {msg, msgStr} = i18n;

    const {url} = kcContext;

    const [isFomSubmittable, setIsFomSubmittable] = useState(false);

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("loginIdpReviewProfileTitle")}>
            <form id="kc-idp-review-profile-form" className={"w-full flex flex-col gap-2"} action={url.loginAction}
                  method="post">
                <UserProfileFormFields
                    kcContext={kcContext}
                    onIsFormSubmittableValueChange={setIsFomSubmittable}
                    i18n={i18n}
                    getClassName={getClassName}
                />

                <Button
                    type="submit"
                    disabled={!isFomSubmittable}
                >
                    {msgStr("doSubmit")}
                </Button>
            </form>
        </Template>
    );
}