import {useState} from "react";
import {clsx} from "keycloakify/tools/clsx";
import {useConstCallback} from "keycloakify/tools/useConstCallback";
import type {FormEventHandler} from "react";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Input} from "../../components/ui/input";
import {Label} from "../../components/ui/label";
import {Button} from "../../components/ui/button";
import {useTitle} from "react-use";

export default function LoginPassword(props: PageProps<Extract<KcContext, { "pageId": "login-password.ftl" }>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {realm, url, login, messagesPerField  } = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(i18n.msgStr("loginTitle", kcContext.realm.displayName))
    const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);

    const onSubmit = useConstCallback<FormEventHandler<HTMLFormElement>>(e => {
        e.preventDefault();

        setIsLoginButtonDisabled(true);

        const formElement = e.target as HTMLFormElement;

        formElement.submit();
    });

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("doLogIn")}>
            <div className="flex gap-4 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center md:w-[350px]`}>
                    <form id="kc-form-login" onSubmit={onSubmit} action={url.loginAction} method="post"
                          className={"grid gap-3 w-full items-center"}>
                        <div className={"w-full"}>
                            <Label htmlFor="password" className={getClassName("kcLabelClass")}>
                                {msg("password")}
                            </Label>
                            <Input
                                tabIndex={2}
                                id="password"
                                className={getClassName("kcInputClass")}
                                name="password"
                                type="password"
                                autoFocus={true}
                                autoComplete="on"
                                defaultValue={login.password ?? ""}
                            />
                            {messagesPerField.get("password") && <div
                                className={"text-xs text-red-500"}>{messagesPerField.get("password")}</div>}
                            {realm.resetPasswordAllowed && (
                                <span className={"text-sm mt-1 text-slate-500 mb-3"}>
                                            <a tabIndex={5} href={url.loginResetCredentialsUrl}>
                                                {msg("doForgotPassword")}
                                            </a>
                                        </span>
                            )}
                        </div>
                        <div id="kc-form-buttons">
                            <Button
                                tabIndex={4}
                                className={"w-full"}
                                name="login"
                                id="kc-login"
                                type="submit"
                                disabled={isLoginButtonDisabled}
                            >
                                {msgStr("doLogIn")}
                            </Button>
                        </div>
                    </form>

                </div>
            </div>
        </Template>
    );
}