import {clsx} from "keycloakify/tools/clsx";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Button} from "../../components/ui/button";
import {IconQuestionMark} from "@tabler/icons-react";

export default function LogoutConfirm(props: PageProps<Extract<KcContext, { pageId: "logout-confirm.ftl" }>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {url, client, logoutConfirm} = kcContext;

    const {msg, msgStr} = i18n;


    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} displayMessage={false}
                  headerNode={msg("logoutConfirmTitle")}>
            <div id="kc-logout-confirm" className={"w-full flex flex-col items-center"}>
                <h2 className="mb-2 border-slate-900 border-2 py-2 px-24  rounded-lg shadow-md">
                  {msg("logoutConfirmHeader")}</h2>
                <form className="w-full flex flex-col items-center justify-center gap-2"
                      action={url.logoutConfirmAction} method="POST">
                    <input type="hidden" name="session_code" value={logoutConfirm.code}/>
                    <Button
                        tabIndex={4}
                        name="confirmLogout"
                        id="kc-logout"
                        type="submit"
                        className={"min-w-[200px]"}
                        variant={"destructive"}
                    >
                        {msgStr("doLogout")}
                    </Button>
                    {!logoutConfirm.skipLink && client.baseUrl && (
                        <>
                            {/*@ts-ignore*/}
                            <Button variant={"ghost"} onClick={() => window.location.href = client.baseUrl}
                                    className={"min-w-[200px]"}>
                                {msg("backToApplication")}
                            </Button>
                        </>
                    )}
                </form>
            </div>
        </Template>
    );
}