import {useState, type FormEventHandler, useEffect, useRef} from "react";
import {clsx} from "keycloakify/tools/clsx";
import {useConstCallback} from "keycloakify/tools/useConstCallback";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Input} from "components/ui/input";
import {Button, buttonVariants} from "../../components/ui/button";
import {Checkbox} from "../../components/ui/checkbox";
import {cn} from "../../lib/utils";
import {Label} from "../../components/ui/label";
import {Separator} from "../../components/ui/separator";
import {GitHubLogoIcon} from '@radix-ui/react-icons'
import {
    IconBrandBitbucket,
    IconBrandFacebookFilled, IconBrandGithub, IconBrandGitlab,
    IconBrandGoogle, IconBrandInstagram, IconBrandLinkedin, IconBrandOpenSource, IconBrandPaypal,
    IconBrandStackoverflow,
    IconBrandTwitter,
    IconBrandWindows
} from "@tabler/icons-react";
import {useTitle} from "react-use";
import {base64url} from "rfc4648";
import {assert} from "tsafe/assert";
import {is} from "tsafe/is";
import {typeGuard} from "tsafe/typeGuard";

const my_custom_param = new URL(window.location.href).searchParams.get("my_custom_param");

if (my_custom_param !== null) {
    console.log("my_custom_param:", my_custom_param);
}
const prividerIdIconMap = {
    "microsoft": <IconBrandWindows/>,
    "google": <IconBrandGoogle/>,
    "facebook": <IconBrandFacebookFilled/>,
    "twitter": <IconBrandTwitter/>,
    "stackoverflow": <IconBrandStackoverflow/>,
    "gitlab": <IconBrandGitlab/>,
    "paypal": <IconBrandPaypal/>,
    "instagram": <IconBrandInstagram/>,
    "linkedin": <IconBrandLinkedin/>,
    "github": <IconBrandGithub/>,
    "bitbucket": <IconBrandBitbucket/>,
    "openshift": <IconBrandOpenSource/>
}

export default function LoginPasskeyOrPassword(props: PageProps<Extract<KcContext, {
    pageId: "passkey-login-form.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {
        social,
        rpId,
        userVerification,
        createTimeout,
        challenge,
        messagesPerField,
        realm,
        url,
        usernameHidden,
        login,
        auth,
        registrationDisabled
    } = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(i18n.msgStr("loginTitle", kcContext.realm.displayName))
    const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);
    const [passkeyButtonDisabled] = useState(!Boolean(window.PublicKeyCredential))
    const onSubmit = useConstCallback<FormEventHandler<HTMLFormElement>>(e => {
        e.preventDefault();

        setIsLoginButtonDisabled(true);

        const formElement = e.target as HTMLFormElement;

        //NOTE: Even if we login with email Keycloak expect username and password in
        //the POST request.
        formElement.querySelector("input[name='email']")?.setAttribute("name", "username");

        formElement.submit();
    });
    const [error, setError] = useState("");
    const formElementRef = useRef<HTMLFormElement>(null);
    const [controller, setController] = useState(new AbortController());
    const submitForm = async (): Promise<void> => {
        const formElement = formElementRef.current;

        if (formElement === null) {
            await new Promise(resolve => setTimeout(resolve, 100));
            return submitForm();
        }

        formElement.submit();
    };
    const [webauthnResult, setWebauthnResult] = useState({} as any)
    useEffect(() => {
        if (error != null && webauthnResult.authenticatorData) {
            console.log(webauthnResult)
            submitForm()
        }
    }, [webauthnResult])
    const webAuthnAuthenticate = useConstCallback(async (additionalOptions: any) => {
        // if (!window.PublicKeyCredential) {
        //     setError(msgStr("webauthn-unsupported-browser-text"));
        //     submitForm();
        //     return;
        // }


        const publicKey: PublicKeyCredentialRequestOptions = {
            rpId,
            challenge: base64url.parse(challenge, {loose: true})
        };

        if (createTimeout !== 0) {
            publicKey.timeout = createTimeout * 1000;
        }

        if (userVerification !== "not specified") {
            publicKey.userVerification = userVerification;
        }

        try {
            const result = await navigator.credentials.get({
                publicKey,
                signal: controller.signal,
                ...additionalOptions
            });
            if (!result || result.type != "public-key") {
                console.log("current from ", {
                    publicKey,
                    signal: controller.signal,
                    ...additionalOptions
                })
                return;
            }
            assert(is<PublicKeyCredential>(result));
            if (!("authenticatorData" in result.response)) {
                return;
            }
            const response = result.response;

            const clientDataJSON = response.clientDataJSON;


            assert(
                //@ts-ignore
                typeGuard<AuthenticatorAssertionResponse>(response, "signature" in response && response.authenticatorData instanceof ArrayBuffer),
                "response not an AuthenticatorAssertionResponse"
            );

            const authenticatorData = response.authenticatorData;
            const signature = response.signature;
            setWebauthnResult({
                clientDataJSON: base64url.stringify(new Uint8Array(clientDataJSON), {"pad": false}),
                authenticatorData: base64url.stringify(new Uint8Array(authenticatorData), {"pad": false}),
                signature: base64url.stringify(new Uint8Array(signature), {"pad": false}),
                credentialId: result.id,
                userHandle: base64url.stringify(new Uint8Array(response.userHandle!), {"pad": false})
            })
        } catch (err) {
            setError(String(err));
        }
    });

    async function tryAutoFillUI() {
        //@ts-ignore
        let isConditionalMediationAvailable = await PublicKeyCredential.isConditionalMediationAvailable();
        if (isConditionalMediationAvailable) {
            webAuthnAuthenticate({mediation: 'conditional'});
        } else {
        }
    }

    useEffect(() => {
        if (!window.PublicKeyCredential) {
            return;
        }
        //@ts-ignore
        if (typeof PublicKeyCredential.isConditionalMediationAvailable === "undefined") {

        } else {
            tryAutoFillUI();
        }
    }, []);
    const startNormalLogin = () => {
        controller.abort();
        webAuthnAuthenticate({})
    }
    return (
        <Template
            {...{kcContext, i18n, doUseDefaultCss, classes}}
            displayInfo={social.displayInfo}
            displayWide={realm.password && social.providers !== undefined}
            headerNode={msg("doLogIn")}
            infoNode={
                realm.password &&
                realm.registrationAllowed &&
                !registrationDisabled && (
                    <div id="kc-registration" className={"justify-center w-full items-center text-center mb-3"}>
                        <Separator className={"mb-1 w-full mt-1"}/>
                        <span className={"text-slate-500 text-sm"}>
                            {msg("noAccount")} {' '}
                            <a tabIndex={6} href={url.registrationUrl} className={"text-slate-950"}>
                                {msg("doRegister")}
                            </a>
                        </span>
                    </div>
                )
            }
        >

            <div className=" lg:pb-4 flex gap-4 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center md:w-[350px] `}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    <form id="kc-form-login" onSubmit={onSubmit} action={url.loginAction} method="post"
                          className={"flex flex-col gap-3 items-center w-full md:min-w-[350px]"}>
                        <div className={"w-full flex flex-col items-center justify-center"}>
                            {!usernameHidden &&
                                (() => {
                                    const label = !realm.loginWithEmailAllowed
                                        ? "username"
                                        : realm.registrationEmailAsUsername
                                            ? "email"
                                            : "usernameOrEmail";

                                    const autoCompleteHelper: typeof label = label === "usernameOrEmail" ? "username" : label;

                                    return (
                                        <div className="grid w-full max-w-sm items-center gap-1.5">
                                            <Label htmlFor={autoCompleteHelper}
                                                   className={getClassName("kcLabelClass")}>
                                                {msg(label)}
                                            </Label>
                                            <Input
                                                tabIndex={1}
                                                id={autoCompleteHelper}

                                                name={`${autoCompleteHelper}`}
                                                type="text"
                                                autoFocus={true}
                                                autoComplete={`${autoCompleteHelper} webauthn`}
                                            />
                                            {messagesPerField.get("username") && <div
                                                className={"text-xs text-red-500"}>{messagesPerField.get("username")}</div>}
                                        </div>
                                    );
                                })()}
                        </div>

                        <div id="kc-form-options">
                            {realm.rememberMe && !usernameHidden && (
                                <div className="flex items-center space-x-2 gap-2">
                                    <Checkbox
                                        tabIndex={3}
                                        id="rememberMe"
                                        name="rememberMe"
                                        defaultChecked={false}
                                    />
                                    <label
                                        htmlFor="rememberMe"
                                        className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                                    >
                                        {msg("rememberMe")}
                                    </label>
                                </div>
                            )}
                        </div>


                        <Button
                            tabIndex={4}
                            name="login"
                            id="kc-login"
                            type="submit"
                            className={"w-full"}
                            disabled={isLoginButtonDisabled}
                        >
                            {msgStr("doLogIn")}
                        </Button>


                    </form>
                    <Separator className={"mt-3"}/>
                    <form id="kc-form-login" onSubmit={onSubmit} action={url.loginAction} method="post"
                          className={"flex flex-col gap-3 items-center w-full md:min-w-[350px] pt-3"}
                          ref={formElementRef}>
                        <input type="hidden" id="clientDataJSON" name="clientDataJSON"
                               value={webauthnResult.clientDataJSON}/>
                        <input type="hidden" id="authenticatorData" name="authenticatorData"
                               value={webauthnResult.authenticatorData}/>
                        <input type="hidden" id="signature" name="signature" value={webauthnResult.signature}/>
                        <input type="hidden" id="credentialId" name="credentialId" value={webauthnResult.credentialId}/>
                        <input type="hidden" id="userHandle" name="userHandle" value={webauthnResult.userHandle}/>
                        <input type="hidden" id="error" name="error" value={webauthnResult.error}/>

                        <Button
                            id="authenticateWebAuthnButton"
                            type="button"
                            onClick={webAuthnAuthenticate}
                            autoFocus={true}
                            disabled={passkeyButtonDisabled}
                            variant={"ghost"}
                            className={"w-full"}
                        >
                            {msgStr("webauthn-doAuthenticate")}
                        </Button>
                    </form>
                </div>
                {realm.password && social.providers !== undefined && (
                    <div
                        id="kc-social-providers"
                        className={'flex gap-4 w-auto'}
                    >
                        <Separator orientation="vertical"/>
                        <div
                            className={"grid flex-col flex-wrap grid-cols-2 gap-2 w-[320px]"}
                        >
                            {social.providers.map(p => (
                                <Button onClick={() => window.location.href = p.loginUrl} id={`zocial-${p.alias}`}
                                        key={p.providerId}
                                        className={`${buttonVariants({variant: "secondary"})} w-[160px] flex justify-start gap-2`}>
                                    {/*@ts-ignore*/}
                                    {prividerIdIconMap[p.providerId]}
                                    <span>{p.displayName}</span>
                                </Button>
                            ))}
                        </div>
                    </div>
                )}
            </div>


        </Template>
    );
}
