import {clsx} from "keycloakify/tools/clsx";
import {I18n} from "../i18n";
import {KcContext} from "../kcContext";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";

import {PinField} from "react-pin-field";
import {Input} from "../../components/ui/input";
import {Button} from "../../components/ui/button";
import {Label} from "../../components/ui/label";
import {PageProps} from "keycloakify/login";

export default function LoginDeviceVerifyUserCode(props: PageProps<Extract<KcContext, {
    pageId: "login-oauth2-device-verify-user-code.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, classes, Template} = props;
    const {url} = kcContext;

    const {msg, msgStr} = i18n;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("oauth2DeviceVerificationTitle")}>
             <div className="flex gap-4 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center md:w-[350px]`}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
            <form
                id="kc-user-verify-device-user-code-form"
                className={"flex flex-col w-full gap-2 "}
                action={url.oauth2DeviceVerificationAction}
                method="post"
            >
                <div className="flex items-center w-full flex-col gap-1.5">
                    <Label htmlFor="device-user-code">
                        {msg("verifyOAuth2DeviceUserCode")}
                    </Label>
                    <Input
                        id="device-user-code"
                        name="device_user_code"
                        autoComplete="off"
                        type="text"
                        className={getClassName("kcInputClass")}
                        autoFocus
                    />
                </div>


                <Button
                    className={clsx(
                        getClassName("kcButtonClass"),
                        getClassName("kcButtonPrimaryClass"),
                        getClassName("kcButtonLargeClass")
                    )}
                    type="submit"

                >
                    {msgStr("doSubmit")}
                </Button>
            </form>
            </div></div>
        </Template>
    );
}