import {clsx} from "keycloakify/tools/clsx";
import {KcContext} from "../kcContext";
import {I18n} from "../i18n";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import {Button} from "../../components/ui/button";
import {PageProps} from "keycloakify/login";

export default function LoginOauthGrant(props: PageProps<Extract<KcContext, {
    pageId: "login-oauth-grant.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, classes, Template} = props;
    const {url, oauth, client} = kcContext;

    const {msg, msgStr, advancedMsg, advancedMsgStr} = i18n;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    return (
        <Template
            {...{kcContext, i18n, doUseDefaultCss, classes}}
            headerNode={msg("oauthGrantTitle", client.name ? advancedMsgStr(client.name) : client.clientId)}
        >
            <div id="kc-oauth" className="flex flex-col gap-2 w-full items-center">
                <h2>{msg("oauthGrantRequest")}</h2>
                <div className={"border-2 bg-slate-900 text-white w-full shadow-md rounded-xl"}>
                    <ul className={"list-disc p-2 pl-6"}>
                        {oauth.clientScopesRequested.map((clientScope: any) => (
                            <li key={clientScope.consentScreenText}>
                                <span>{advancedMsg(clientScope.consentScreenText)}</span>
                            </li>
                        ))}
                    </ul>
                </div>

                <form className={"w-full items-center flex flex-col gap-2"} action={url.oauthAction} method="POST">
                    <input type="hidden" name="code" value={oauth.code}/>

                    <Button

                        name="accept"
                        id="kc-login"
                        type="submit"
                        className={"min-w-[200px]"}
                    >
                        {msgStr("doYes")}
                    </Button>
                    <Button
                        variant={"ghost"}
                        name="cancel"
                        id="kc-cancel"
                        className={"min-w-[200px]"}
                        type="submit">
                        {msgStr("doNo")}
                    </Button>


                </form>
            </div>
        </Template>
    );
}