import type {PageProps} from "keycloakify/login/pages/PageProps";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Alert, AlertDescription, AlertTitle} from "../../components/ui/alert";
import {ExclamationTriangleIcon} from "@radix-ui/react-icons";
import {Button} from "../../components/ui/button";
import {useTitle} from "react-use";

export default function WebauthnError(props: PageProps<Extract<KcContext, { pageId: "webauthn-error.ftl" }>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {message, client} = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(msgStr("webauthn-error-title") + kcContext.realm.displayName)
    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} displayMessage={false}
                  headerNode={msg("errorTitle")}>
            <div className={`mx-auto flex w-full flex-col justify-center sm:w-[450px] gap-2 mb-2 items-center`}
                 id="kc-form-wrapper"
                // className={clsx(
                //     realm.password &&
                //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                // )}
            >
                <Alert variant="destructive" className={"mb-2 w-full shadow-md"}>
                    <ExclamationTriangleIcon className="h-4 w-4"/>
                    {/*@ts-ignore*/}
                    <AlertDescription dangerouslySetInnerHTML={{__html: message.summary}}>
                    </AlertDescription>
                </Alert>

                <form id="kc-error-credential-form"
                      action={kcContext.url.loginAction}
                      method="post">
                    <input type="hidden" id="executionValue" name="authenticationExecution" value={"retry"}/>
                    <input type="hidden" id="isSetRetry" name="isSetRetry" value={kcContext.execution}/>
                    <input type="hidden" id="try-again" name="try-again"/>
                    <Button className={"min-w-[200px]"} type={"submit"}>
                        {msg("doTryAgain")}
                    </Button>
                </form>


                {kcContext.isAppInitiatedAction &&
                    <form action={kcContext.url.loginAction}
                          method="post">
                        <Button type="submit"
                                className={"min-w-[200px]"}
                                variant={"ghost"}
                                id="cancelWebAuthnAIA" name="cancel-aia" value="true">{msg("doCancel")}
                        </Button>
                    </form>
                }
                {/*{client !== undefined && client.baseUrl !== undefined && (*/}
                {/*    <p>*/}
                {/*        <a id="backToApplication" href={client.baseUrl}>*/}
                {/*            {msg("backToApplication")}*/}
                {/*        </a>*/}
                {/*    </p>*/}
                {/*)}*/}
            </div>
        </Template>
    );
}