import type {KcContext} from "keycloakify/login/kcContext";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import type {I18n} from "keycloakify/login/i18n";
import {Alert, AlertTitle} from "../../components/ui/alert";
import {IconCircleCheck} from "@tabler/icons-react";
import useCountDown from "../../hooks/useCountDown";
import {Button} from "../../components/ui/button";
import {Separator} from "../../components/ui/separator";

export default function LoginIdpLinkEmail(props: PageProps<Extract<KcContext, {
    pageId: "login-idp-link-email.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {url, realm, brokerContext, idpAlias} = kcContext;

    const {msg} = i18n;
    const [time] = useCountDown({mss: 60});
    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("emailLinkIdpTitle", idpAlias)}>
            <div className={"flex flex-col items-center justify-center gap-2"}>
                <Alert className={"bg-green-200 shadow-md lg:max-w-[450px]"}>
                    <AlertTitle className={"flex gap-1  leading-relaxed items-start"}>
                        <IconCircleCheck/> {msg("emailLinkIdp1", idpAlias, brokerContext.username, realm.displayName)}
                    </AlertTitle>
                </Alert>
                <div className="flex items-center justify-center w-full text-sm">
                    <div>
                        {msg("emailVerifyInstruction2")}
                        <Button onClick={() => window.location.href = url.loginAction} variant={"link"}
                                disabled={time > 0} className={"whitespace-pre"}>
                            {msg("emailVerifyInstruction3")} {time > 0 && <span> ({time})</span>}
                        </Button>
                    </div>
                </div>
                <Separator/>
                <div className="flex items-center justify-center w-full text-sm">
                    <div>

                        <Button onClick={() => window.location.href = url.loginAction} variant={"link"}
                                className={"px-1"}>
                            <span>{msg("emailLinkIdp4")}, {msg("doClickHere")}</span>
                        </Button>
                    </div>
                </div>
            </div>
        </Template>
    );
}