import type {PageProps} from "keycloakify/login/pages/PageProps";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {useTitle} from "react-use";
import {Button} from "../../components/ui/button";
import {Alert, AlertDescription, AlertTitle} from "../../components/ui/alert";
import {IconFaceIdError} from "@tabler/icons-react";

export default function Error(props: PageProps<Extract<KcContext, { pageId: "error.ftl" }>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {message, client} = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(`${msgStr("doRegister")} - ${kcContext.realm.displayName}`)
    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} displayMessage={false}
                  headerNode={msg("errorTitle")}>
            <div id="kc-error-message" className={"w-full flex flex-col gap-2 md:min-w-[450px]"}>
                <Alert className="shadow-md" variant={"destructive"}>
                    <AlertTitle className={"flex gap-1 items-center"}>
                        <IconFaceIdError/> {msgStr("errorTitle")}
                    </AlertTitle>
                    <AlertDescription>
                        {message.summary}
                    </AlertDescription>
                </Alert>
                {client !== undefined && client.baseUrl !== undefined && (
                    <>

                        <Button id="backToApplication"
                            //@ts-ignore
                                onClick={() => window.location.href = client!.baseUrl}>
                            {msg("backToApplication")}
                        </Button>
                    </>
                )}
            </div>
        </Template>
    );
}