import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "keycloakify/login/kcContext";
import type {I18n} from "keycloakify/login/i18n";
import {MouseEvent, useRef} from "react";
import {useConstCallback} from "keycloakify/tools/useConstCallback";
import {Card, CardContent, CardTitle} from "../../components/ui/card";
import {IconPassword, IconKey, IconNumbers, IconLock, IconUser} from '@tabler/icons-react'
import {useTitle} from "react-use";


const IconMap = {
    "kcAuthenticatorPasswordClass": <IconPassword/>,
    "kcAuthenticatorOTPClass": <IconNumbers/>,
    kcAuthenticatorWebAuthnClass: <IconKey/>,
    kcAuthenticatorWebAuthnPasswordlessClass: <IconKey/>,
    "kcAuthenticatorDefaultClass": <IconUser/>

}
export default function SelectAuthenticator(props: PageProps<Extract<KcContext, {
    pageId: "select-authenticator.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;
    const {url, auth} = kcContext;

    const {getClassName} = useGetClassName({doUseDefaultCss, classes});
    const {msg, msgStr} = i18n;
    useTitle(`${msgStr("selectAnOption")} - ${kcContext.realm.displayName}`)

    const selectCredentialsForm = useRef<HTMLFormElement>(null);
    const authExecIdInput = useRef<HTMLInputElement>(null);

    const submitForm = useConstCallback(() => {
        selectCredentialsForm.current?.submit();
    });

    const onSelectedAuthenticator = useConstCallback((event: MouseEvent<HTMLDivElement>) => {
        const divElement = event.currentTarget;
        const authExecId = divElement.dataset.authExecId;

        if (!authExecIdInput.current || !authExecId) {
            return;
        }

        authExecIdInput.current.value = authExecId;
        submitForm();
    });

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("loginChooseAuthenticator")}>
            <div className="flex gap-4 mb-3 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center sm:w-[450px] `}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    <form
                        id="kc-select-credential-form"
                        className={getClassName("kcFormClass")}
                        ref={selectCredentialsForm}
                        action={url.loginAction}
                        method="post"
                    >
                        <div className={"flex flex-col gap-2"}>
                            {auth.authenticationSelections.map((authenticationSelection, index) => (
                                <Card key={index} className={'p-3 hover:bg-slate-100 cursor-pointer transition-colors'}
                                      onClick={onSelectedAuthenticator}
                                      data-auth-exec-id={authenticationSelection.authExecId}>
                                    <CardTitle className={"flex items-center gap-2"}>
                                        <>
                                            {/*@ts-ignore*/}
                                            {IconMap[authenticationSelection.iconCssClass]}{msg(authenticationSelection.displayName)}
                                        </>
                                    </CardTitle>
                                    <CardContent className={"p-0"}>
                                        {msg(authenticationSelection.helpText)}
                                    </CardContent>
                                </Card>
                            ))}
                            <input type="hidden" id="authexec-hidden-input" name="authenticationExecution"
                                   ref={authExecIdInput}/>
                        </div>
                    </form>
                </div>
            </div>
        </Template>
    );
}