import {clsx} from "keycloakify/tools/clsx";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {MessageKey} from "keycloakify/login/i18n/i18n";
import {Input} from "../../components/ui/input";
import {Button} from "../../components/ui/button";
import {Label} from "../../components/ui/label";
import {Separator} from "../../components/ui/separator";
import {useTitle} from "react-use";
import useBreakpoint from "use-breakpoint";
import {BREAKPOINTS} from "../../lib/utils";

export default function LoginConfigTotp(props: PageProps<Extract<KcContext, {
    pageId: "login-config-totp.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });
    const {url, isAppInitiatedAction, totp, mode, messagesPerField} = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(`${msgStr("loginTotpTitle")} - ${kcContext.realm.displayName}`)

    const algToKeyUriAlg: Record<(typeof kcContext)["totp"]["policy"]["algorithm"], string> = {
        "HmacSHA1": "SHA1",
        "HmacSHA256": "SHA256",
        "HmacSHA512": "SHA512"
    };

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("loginTotpTitle")}>
            <div className={"flex flex-col md:flex-row"}>
                <div className={"flex flex-col gap-2"}>
                    <p className={"max-w-[350px]"}>{msg("loginTotpStep1")}</p>
                    <ul id="kc-totp-supported-apps" className={"text-sm pl-6"}>
                        {totp.supportedApplications.filter(item => Boolean(item.trim())).map(app => (
                            <li className={"list-disc"}>{msg(app as MessageKey)}</li>
                        ))}
                    </ul>
                    {mode && mode === "manual" ? (
                        <>
                            <p>{msg("loginTotpManualStep2")}</p>
                            <div className={"w-full"}>
                                  <div id="kc-totp-secret-key"
                                       className={"bg-slate-900 rounded-lg p-2 text-white shadow-md text-center"}>{totp.totpSecretEncoded}</div>
                            </div>

                            <p>
                                <Button onClick={() => window.location.href = totp.qrUrl} id="mode-barcode">
                                    {msg("loginTotpScanBarcode")}
                                </Button>
                            </p>
                            <p>{msg("loginTotpManualStep3")}</p>
                            <p className={"border-2 p-2 border-slate-600"}>
                                <ul className={"text-sm"}>
                                    <li id="kc-totp-type">
                                        {msg("loginTotpType")}: {msg(`loginTotp.${totp.policy.type}`)}
                                    </li>
                                    <li id="kc-totp-algorithm">
                                        {msg("loginTotpAlgorithm")}: {algToKeyUriAlg?.[totp.policy.algorithm] ?? totp.policy.algorithm}
                                    </li>
                                    <li id="kc-totp-digits">
                                        {msg("loginTotpDigits")}: {totp.policy.digits}
                                    </li>
                                    {totp.policy.type === "totp" ? (
                                        <li id="kc-totp-period">
                                            {msg("loginTotpInterval")}: {totp.policy.period}
                                        </li>
                                    ) : (
                                        <li id="kc-totp-counter">
                                            {msg("loginTotpCounter")}: {totp.policy.initialCounter}
                                        </li>
                                    )}
                                </ul>
                            </p>

                        </>
                    ) : (
                        <>
                            <p>{msg("loginTotpStep2")}</p>
                            <img id="kc-totp-secret-qr-code" src={`data:image/png;base64, ${totp.totpSecretQrCode}`}
                                 alt="Figure: Barcode" className={"border-8 w-64 shadow-lg"}/>
                            <Button variant={"link"} onClick={() => {
                                window.location.href = totp.manualUrl
                            }} id="mode-manual">
                                {msg("loginTotpUnableToScan")}
                            </Button>
                        </>
                    )}


                </div>
                <Separator orientation={"vertical"} className={"mx-10 h-auto"}/>
                <div className={"flex items-center"}>
                    <form action={url.loginAction} className={"flex flex-col gap-4"} id="kc-totp-settings-form"
                          method="post">
                        <div className={getClassName("kcFormGroupClass")}>
                            <div className={getClassName("kcInputWrapperClass")}>
                                <p className={"text-sm"}>{msg("loginTotpStep3")}</p>
                                <Label htmlFor="totp" className={getClassName("kcLabelClass")}>
                                    {msg("authenticatorCode")} <span className="text-red-500">*</span>
                                </Label>
                                <Input
                                    type="text"
                                    id="totp"
                                    name="totp"
                                    autoComplete="off"
                                    className={getClassName("kcInputClass")}
                                    aria-invalid={messagesPerField.existsError("totp")}
                                />

                                {messagesPerField.existsError("totp") && (
                                    <span id="input-error-otp-code" className={getClassName("kcInputErrorMessageClass")}
                                          aria-live="polite">
                                    {messagesPerField.get("totp")}
                                </span>
                                )}
                            </div>
                            <input type="hidden" id="totpSecret" name="totpSecret" value={totp.totpSecret}/>
                            {mode && <input type="hidden" id="mode" value={mode}/>}
                        </div>

                        <div className={getClassName("kcInputWrapperClass")}>

                            <p className={"text-sm"}>{msg("loginTotpStep3DeviceName")}</p>
                            <Label htmlFor="userLabel" className={getClassName("kcLabelClass")}>
                                {msg("loginTotpDeviceName")} {totp.otpCredentials.length >= 1 &&
                                <span className="text-red-500">*</span>}
                            </Label>
                            <Input
                                type="text"
                                id="userLabel"
                                name="userLabel"
                                autoComplete="off"
                                className={getClassName("kcInputClass")}
                                aria-invalid={messagesPerField.existsError("userLabel")}
                            />
                            {messagesPerField.existsError("userLabel") && (
                                <span id="input-error-otp-label"
                                      className={getClassName("kcInputErrorMessageClass")}
                                      aria-live="polite">
                                    {messagesPerField.get("userLabel")}
                                </span>
                            )}
                        </div>

                        {isAppInitiatedAction ? (
                            <div className={'flex flex-col gap-2'}>
                                <Button
                                    type="submit"
                                    className={clsx(
                                        getClassName("kcButtonClass"),
                                        getClassName("kcButtonPrimaryClass"),
                                        getClassName("kcButtonLargeClass")
                                    )}
                                    id="saveTOTPBtn"
                                >
                                    {msgStr("doSubmit")}
                                </Button>
                                <Button
                                    type="submit"
                                    variant={"ghost"}
                                    className={clsx(
                                        getClassName("kcButtonClass"),
                                        getClassName("kcButtonDefaultClass"),
                                        getClassName("kcButtonLargeClass"),
                                        getClassName("kcButtonLargeClass")
                                    )}
                                    id="cancelTOTPBtn"
                                    name="cancel-aia"
                                    value="true"
                                >
                                    {msg("doCancel")}
                                </Button>
                            </div>
                        ) : (
                            <input
                                type="submit"
                                className={clsx(getClassName("kcButtonClass"), getClassName("kcButtonPrimaryClass"), getClassName("kcButtonLargeClass"))}
                                id="saveTOTPBtn"
                                value={msgStr("doSubmit")}
                            />
                        )}
                    </form>
                </div>
            </div>
        </Template>
    );
}