import { useState, type FormEventHandler } from "react";
import { clsx } from "keycloakify/tools/clsx";
import { useConstCallback } from "keycloakify/tools/useConstCallback";
import type { PageProps } from "keycloakify/login/pages/PageProps";
import { useGetClassName } from "keycloakify/login/lib/useGetClassName";
import type { KcContext } from "../kcContext";
import type { I18n } from "../i18n";
import { Input } from "components/ui/input";
import { Button, buttonVariants } from "../../components/ui/button";
import { Checkbox } from "../../components/ui/checkbox";
import { cn } from "../../lib/utils";
import { Label } from "../../components/ui/label";
import { Separator } from "../../components/ui/separator";
import { GitHubLogoIcon } from '@radix-ui/react-icons'
import {
    IconBrandBitbucket,
    IconBrandFacebookFilled, IconBrandGithub, IconBrandGitlab,
    IconBrandGoogle, IconBrandInstagram, IconBrandLinkedin, IconBrandOpenSource, IconBrandPaypal,
    IconBrandStackoverflow,
    IconBrandTwitter,
    IconBrandWindows
} from "@tabler/icons-react";
import { useTitle } from "react-use";

const my_custom_param = new URL(window.location.href).searchParams.get("my_custom_param");

if (my_custom_param !== null) {
    console.log("my_custom_param:", my_custom_param);
}
const prividerIdIconMap = {
    "microsoft": <IconBrandWindows />,
    "google": <IconBrandGoogle />,
    "facebook": <IconBrandFacebookFilled />,
    "twitter": <IconBrandTwitter />,
    "stackoverflow": <IconBrandStackoverflow />,
    "gitlab": <IconBrandGitlab />,
    "paypal": <IconBrandPaypal />,
    "instagram": <IconBrandInstagram />,
    "linkedin": <IconBrandLinkedin />,
    "github": <IconBrandGithub />,
    "bitbucket": <IconBrandBitbucket />,
    "openshift": <IconBrandOpenSource />
}

export default function LoginUsername(props: PageProps<Extract<KcContext, { pageId: "login-username.ftl" }>, I18n>) {
    const { kcContext, i18n, doUseDefaultCss, Template, classes } = props;

    const { getClassName } = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const { social, realm, url, usernameHidden, login, auth, registrationDisabled } = kcContext;

    const { msg, msgStr } = i18n;
    useTitle(i18n.msgStr("loginTitle", kcContext.realm.displayName))
    const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);

    const onSubmit = useConstCallback<FormEventHandler<HTMLFormElement>>(e => {
        e.preventDefault();

        setIsLoginButtonDisabled(true);

        const formElement = e.target as HTMLFormElement;

        //NOTE: Even if we login with email Keycloak expect username and password in
        //the POST request.
        formElement.querySelector("input[name='email']")?.setAttribute("name", "username");

        formElement.submit();
    });

    return (
        <Template
            {...{ kcContext, i18n, doUseDefaultCss, classes }}
            displayInfo={social.displayInfo}
            displayWide={realm.password && social.providers !== undefined}
            headerNode={msg("doLogIn")}
            infoNode={
                realm.password &&
                realm.registrationAllowed &&
                !registrationDisabled && (
                    <div id="kc-registration" className={"justify-center w-full items-center text-center mb-3"}>
                        <Separator className={"mb-1 w-full mt-1"} />
                        <span className={"text-slate-500 text-sm"}>
                            {msg("noAccount")} {' '}
                            <a tabIndex={6} href={url.registrationUrl} className={"text-slate-950"}>
                                {msg("doRegister")}
                            </a>
                        </span>
                    </div>
                )
            }
        >

            <div className="     flex gap-4 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center md:w-[350px] `}
                    id="kc-form-wrapper"
                // className={clsx(
                //     realm.password &&
                //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                // )}
                >
                    {realm.password && (
                        <form id="kc-form-login" onSubmit={onSubmit} action={url.loginAction} method="post"
                        className={"flex flex-col gap-3 items-center w-full md:min-w-[350px]"}>
                            <div className={"w-full flex flex-col items-center justify-center"}>
                                {!usernameHidden &&
                                    (() => {
                                        const label = !realm.loginWithEmailAllowed
                                            ? "username"
                                            : realm.registrationEmailAsUsername
                                                ? "email"
                                                : "usernameOrEmail";

                                        const autoCompleteHelper: typeof label = label === "usernameOrEmail" ? "username" : label;

                                        return (
                                            <div className="grid w-full max-w-sm items-center gap-1.5">
                                                <Label htmlFor={autoCompleteHelper}
                                                    className={getClassName("kcLabelClass")}>
                                                    {msg(label)}
                                                </Label>
                                                <Input
                                                    tabIndex={1}
                                                    id={autoCompleteHelper}
                                                
                                                    //NOTE: This is used by Google Chrome auto fill so we use it to tell
                                                    //the browser how to pre fill the form but before submit we put it back
                                                    //to username because it is what keycloak expects.
                                                    name={autoCompleteHelper}
                                                    defaultValue={login.username ?? ""}
                                                    type="text"
                                                    autoFocus={true}
                                                    autoComplete="off"
                                                />
                                            </div>
                                        );
                                    })()}
                            </div>
                         
                                <div id="kc-form-options">
                                    {realm.rememberMe && !usernameHidden && (
                                        <div className="flex items-center space-x-2 gap-2">
                                            <Checkbox
                                                tabIndex={3}
                                                id="rememberMe"
                                                name="rememberMe"
                                                defaultChecked={login.rememberMe === "on"}
                                            />
                                            <label
                                                htmlFor="rememberMe"
                                                className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                                            >
                                                {msg("rememberMe")}
                                            </label>
                                        </div>
                                    )}
                                </div>
                        

                                <Button
                                    tabIndex={4}
                                    name="login"
                                    id="kc-login"
                                    type="submit"
                                    className={"w-full"}
                                    disabled={isLoginButtonDisabled}
                                >
                                    {msgStr("doLogIn")}
                                </Button>

                        </form>
                    )}
                </div>
                {realm.password && social.providers !== undefined && (
                    <div
                        id="kc-social-providers"
                        className={'flex gap-4 w-auto'}
                    >
                        <Separator orientation="vertical" />
                        <div
                            className={"grid flex-col flex-wrap grid-cols-2 gap-2 w-[320px]"}
                        >
                            {social.providers.map(p => (
                                <Button onClick={() => window.location.href = p.loginUrl} id={`zocial-${p.alias}`}
                                    key={p.providerId}
                                    className={`${buttonVariants({ variant: "secondary" })} w-[160px] flex justify-start gap-2`}>
                                    {/*@ts-ignore*/}
                                    {prividerIdIconMap[p.providerId]}
                                    <span>{p.displayName}</span>
                                </Button>
                            ))}
                        </div>
                    </div>
                )}
            </div>


        </Template>
    );
}
