import {useEffect, useState} from "react";
import {headInsert} from "keycloakify/tools/headInsert";
import {clsx} from "keycloakify/tools/clsx";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Button} from "../../components/ui/button";
import {Input} from "../../components/ui/input";
import {RadioGroup, RadioGroupItem} from "../../components/ui/radio-group";
import {Label} from "../../components/ui/label";

export default function LoginOtp(props: PageProps<Extract<KcContext, { pageId: "login-otp.ftl" }>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {otpLogin, url} = kcContext;

    const {msg, msgStr} = i18n;
    const [selectedCredentialId, setCredentialId] = useState(otpLogin.userOtpCredentials[0].id ?? "");
    useEffect(() => {
        let isCleanedUp = false;

        const {prLoaded, remove} = headInsert({
            "type": "javascript",
            "src": `${kcContext.url.resourcesCommonPath}/node_modules/jquery/dist/jquery.min.js`
        });

        (async () => {
            await prLoaded;

            if (isCleanedUp) {
                return;
            }

            evaluateInlineScript();
        })();

        return () => {
            isCleanedUp = true;
            remove();
        };
    }, []);

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("loginOtpOneTime")}>
            <div className="flex gap-4">
                <div className={`mx-auto flex w-full flex-col justify-center `}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    <form id="kc-otp-login-form" className={"flex flex-col gap-3 items-center w-full min-w-[350px]"}
                          action={url.loginAction}
                          method="post">
                        {otpLogin.userOtpCredentials.length > 1 && (
                            <>
                                <p className={"text-sm text-left w-full"}>
                                    {msgStr("otp-display-name")}
                                </p>
                                <RadioGroup defaultValue={otpLogin.userOtpCredentials[0].id}
                                            className={"items-start w-full"} name="selectedCredentialId">
                                    {otpLogin.userOtpCredentials.map(otpCredential => (
                                        <div className="flex items-center space-x-2">
                                            <RadioGroupItem value={otpCredential.id} id="option-one"/>
                                            <Label htmlFor="option-one">{otpCredential.userLabel}</Label>
                                        </div>
                                    ))}

                                </RadioGroup>
                            </>
                        )}

                        <div className={"w-full"}>

                            <Label htmlFor="otp" className={getClassName("kcLabelClass")}>
                                {msg("loginOtpOneTime")}
                            </Label>


                            <Input id="otp" name="otp" autoComplete="off" type="text"
                                   className={getClassName("kcInputClass")} autoFocus/>

                        </div>



                            <Button
                                name="login"
                                id="kc-login"
                                type="submit"
                                className={"w-full"}
                            >
                                {msgStr("doLogIn")}
                            </Button>

                    </form>
                </div>
            </div>
        </Template>
    );
}

declare const $: any;

function evaluateInlineScript() {
    $(document).ready(function () {
        // Card Single Select
        $(".card-pf-view-single-select").click(function (this: any) {
            if ($(this).hasClass("active")) {
                $(this).removeClass("active");
                $(this).children().removeAttr("name");
            } else {
                $(".card-pf-view-single-select").removeClass("active");
                $(".card-pf-view-single-select").children().removeAttr("name");
                $(this).addClass("active");
                $(this).children().attr("name", "selectedCredentialId");
            }
        });

        var defaultCred = $(".card-pf-view-single-select")[0];
        if (defaultCred) {
            defaultCred.click();
        }
    });
}