import {clsx} from "keycloakify/tools/clsx";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {useTitle} from "react-use";
import {Input} from "../../components/ui/input";
import {Label} from "../../components/ui/label";
import {Checkbox} from "../../components/ui/checkbox";
import {Button} from "../../components/ui/button";

export default function LoginUpdatePassword(props: PageProps<Extract<KcContext, {
    pageId: "login-update-password.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {msg, msgStr} = i18n;
    useTitle(`${msgStr("updatePasswordTitle")} - ${kcContext.realm.displayName}`)
    const {url, messagesPerField, isAppInitiatedAction, username} = kcContext;

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} headerNode={msg("updatePasswordTitle")}>
            {/*    <div*/}
            <div className="flex gap-4 w-full">
                <div className={`mx-auto flex w-full flex-col justify-center md:w-[350px]`}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    <form id="kc-passwd-update-form" className={"flex gap-3 flex-col items-center w-full"}
                          action={url.loginAction}
                          method="post">
                        <input
                            type="text"
                            id="username"
                            name="username"
                            value={username}
                            readOnly={true}
                            autoComplete="username"
                            style={{display: "none"}}
                        />
                        <Input type="password" id="password" name="password" autoComplete="current-password"
                               style={{display: "none"}}/>

                        <div
                            className={"w-full"}
                        >
                            <Label htmlFor="password-new" className={getClassName("kcLabelClass")}>
                                {msg("passwordNew")}
                            </Label>


                            <Input
                                type="password"
                                id="password-new"
                                name="password-new"
                                autoFocus
                                autoComplete="new-password"
                                className={getClassName("kcInputClass")}
                            />

                        </div>

                        <div
                            className={"w-full"}
                        >

                            <Label htmlFor="password-confirm" className={getClassName("kcLabelClass")}>
                                {msg("passwordConfirm")}
                            </Label>


                            <Input
                                type="password"
                                id="password-confirm"
                                name="password-confirm"
                                autoComplete="new-password"
                                className={getClassName("kcInputClass")}
                            />

                        </div>


                        <div className="flex items-center space-x-2">
                            <Checkbox defaultChecked id="logout-sessions" name="logout-sessions"/>
                            <label
                                htmlFor="logout-sessions"
                                className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                            >
                                {msgStr("logoutOtherSessions")}
                            </label>
                        </div>


                        <div id="kc-form-buttons" className={"w-full"}>
                            {isAppInitiatedAction ? (
                                <div className={"flex gap-2 flex-col"}>
                                    <Button

                                        type="submit"
                                        className={"w-full"}
                                    >
                                        {msgStr("doSubmit")}
                                    </Button>
                                    <Button
                                        variant={"ghost"}
                                        type="submit"
                                        name="cancel-aia"
                                        value="true"
                                        className={"w-full"}
                                    >
                                        {msg("doCancel")}
                                    </Button>
                                </div>
                            ) : (
                                <Button
                                    className={"w-full"}
                                    type="submit"
                                >
                                    {msgStr("doSubmit")}
                                </Button>
                            )}
                        </div>
                    </form>
                </div>
            </div>
        </Template>
    );
}