import type {PageProps} from "keycloakify/login/pages/PageProps";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Alert, AlertDescription, AlertTitle} from "../../components/ui/alert";
import {Button} from "../../components/ui/button";
import {IconCircleCheck} from "@tabler/icons-react";
import {useEffect, useRef, useState} from "react";
import {useInterval} from "react-use";
import useCountDown from "../../hooks/useCountDown";

export default function LoginVerifyEmail(props: PageProps<Extract<KcContext, {
    pageId: "login-verify-email.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {msg} = i18n;

    const {url, user} = kcContext;
    const [time] = useCountDown({mss: 60});


    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} displayMessage={false}
                  headerNode={msg("emailVerifyTitle")}>
            <div className={"flex flex-col item-center justify-center gap-2 lg:w-[450px]"}>
                <Alert className={"bg-green-200 shadow-md"}>
                    <AlertTitle className={"flex gap-1 items-center"}>
                        <IconCircleCheck/>{msg("emailVerifyInstruction1", user?.email ?? "")}
                    </AlertTitle>

                </Alert>
                <div className="flex flex-col items-center justify-center w-full text-sm">
                    {msg("emailVerifyInstruction2")}
                    <Button onClick={() => window.location.href = url.loginAction} variant={"link"}
                            disabled={time > 0} className={"whitespace-pre"}>
                        {msg('doClickHere')}<span> </span>{msg("emailVerifyInstruction3")}{time > 0 && <span> ({time})</span>}
                    </Button>
                </div>

            </div>
        </Template>
    );
}