import type {PageProps} from "keycloakify/login/pages/PageProps";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Button} from "../../components/ui/button";

export default function LoginPageExpired(props: PageProps<Extract<KcContext, {
    pageId: "login-page-expired.ftl"
}>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;

    const {url} = kcContext;

    const {msg} = i18n;

    return (
        <Template {...{kcContext, i18n, doUseDefaultCss, classes}} displayMessage={false}
                  headerNode={msg("pageExpiredTitle")}>
            <div className={"flex flex-col gap-2 w-full"}>
                <div className={"flex gap-2 items-center w-full"}>

                    <Button id="loginRestartLink" className={"w-full"}
                            onClick={() => window.location.href = url.loginRestartFlowUrl}>
                        {msg("pageExpiredMsg1")}
                    </Button>
                </div>

                <div className={"flex gap-2 items-center"}>

                    <Button id="loginContinueLink" variant={"ghost"} className={"w-full"}
                            onClick={() => window.location.href = url.loginAction}>
                        {msg("pageExpiredMsg2")}
                    </Button>
                </div>
            </div>
        </Template>
    );
}